import React from 'react';
import { Box, Typography, IconButton, Paper } from '@mui/material';
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';
import LockIcon from '@mui/icons-material/Lock';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';

function Suporte() {
  const handleWhatsAppClick = () => {
    const phoneNumber = '552730123131'; // Código do país + número
    const message = 'Olá, preciso de suporte!'; // Mensagem inicial
    const whatsappURL = `https://wa.me/${phoneNumber}?text=${encodeURIComponent(message)}`;
    window.open(whatsappURL, '_blank');
  };

  return (
    <Box sx={{ padding: '10px', marginTop: '-20px' }}>
      <Typography variant="h6" sx={{ fontSize: '14px', fontWeight: 'bold', marginBottom: '10px', color: '#5b5b5b' }}>
        Precisa de Suporte?
      </Typography>

      <Paper 
        elevation={0}
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
          backgroundColor: '#f5f5f5',
          padding: '10px 20px',
          borderRadius: '10px',
          marginBottom: '10px',
        }}
        onClick={handleWhatsAppClick} // Adiciona a ação de clique
      >
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <HelpOutlineIcon sx={{ fontSize: 30, marginRight: '15px', color: '#000' }} />
          <Box>
            <Typography sx={{ fontWeight: 'bold', fontSize: '16px' }}>Chame nosso suporte</Typography>
            <Typography sx={{ fontSize: '14px', color: '#888' }}>Tire suas dúvidas</Typography>
          </Box>
        </Box>
        <IconButton>
          <ArrowForwardIosIcon sx={{ fontSize: 20, color: '#888' }} />
        </IconButton>
      </Paper>

      <Paper
        elevation={0}
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
          backgroundColor: '#f5f5f5',
          padding: '10px 20px',
          borderRadius: '10px',
        }}
      >
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <LockIcon sx={{ fontSize: 30, marginRight: '15px', color: '#000' }} />
          <Box>
            <Typography sx={{ fontWeight: 'bold', fontSize: '16px' }}>Alterar senha</Typography>
            <Typography sx={{ fontSize: '14px', color: '#888' }}>Alterar senha do Wi-Fi</Typography>
          </Box>
        </Box>
        <IconButton>
          <ArrowForwardIosIcon sx={{ fontSize: 20, color: '#888' }} />
        </IconButton>
      </Paper>
    </Box>
  );
}

export default Suporte;
