// Header.js
import React, { useState, useEffect } from 'react';
import { Box, Typography, IconButton, Modal, CircularProgress } from '@mui/material';
import { Notifications, ExitToApp } from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import { CircleLoader } from 'react-spinners';

function Header({ dadosCliente }) {
  const navigate = useNavigate();
  const [isLoggingOut, setIsLoggingOut] = useState(false); 

  // Função para formatar o nome
  const formatarNome = (nomeCompleto) => {
    if (!nomeCompleto) return 'Cliente';
    const primeiroNome = nomeCompleto.split(' ')[0];
    return primeiroNome.charAt(0).toUpperCase() + primeiroNome.slice(1).toLowerCase();
  };

  const nomeCliente = formatarNome(
    dadosCliente?.dadosCliente?.['dados_cliente.json']?.razao ||
    dadosCliente?.dadosCliente?.['dados_cliente.json']?.fantasia ||
    'Cliente'
  );

  // Função de logout e limpeza de cache
  const handleLogout = async () => {
    setIsLoggingOut(true);

    try {
      localStorage.clear();

      setTimeout(() => {
        setIsLoggingOut(false);
        navigate('/'); // Redireciona para a tela de login
        window.location.reload();
      }, 500); 
    } catch (error) {
      console.error('Erro durante o logout:', error);
      setIsLoggingOut(false);
    }
  };

  // Lógica para logout automático após 1 hora
  useEffect(() => {
    const logoutTimer = setTimeout(handleLogout, 14400000); // 1 hora = 3600000ms

    const resetTimer = () => {
      clearTimeout(logoutTimer);
      setTimeout(handleLogout, 14400000);
    };

    // Eventos de atividade do usuário
    window.addEventListener('mousemove', resetTimer);
    window.addEventListener('keydown', resetTimer);
    window.addEventListener('scroll', resetTimer);
    window.addEventListener('click', resetTimer);

    return () => {
      clearTimeout(logoutTimer);
      window.removeEventListener('mousemove', resetTimer);
      window.removeEventListener('keydown', resetTimer);
      window.removeEventListener('scroll', resetTimer);
      window.removeEventListener('click', resetTimer);
    };
  }, [navigate]);

  return (
    <Box sx={{ background: '#fff', padding: '20px', display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
      {/* Logotipo e saudação */}
      <Box sx={{ display: 'flex', alignItems: 'center' }}>
        <img
          src="https://i.ibb.co/6v5393R/LOGO-2024-ORIGINAL.png"
          alt="Logo"
          style={{ width: '50px', marginRight: '10px' }}
        />
        <Box>
          <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
            Olá, {nomeCliente}!
          </Typography>
          <Typography variant="body2" sx={{ color: '#6c757d' }}>
            Como Podemos te Ajudar?
          </Typography>
        </Box>
      </Box>

      {/* Ícones de notificações e logout */}
      <Box>
        {isLoggingOut ? (
          <CircularProgress size={24} color="inherit" />
        ) : (
          <>
            <IconButton style={{ color: '#28a745', marginRight: '10px' }}>
              <Notifications />
            </IconButton>
            <IconButton style={{ color: '#28a745' }} onClick={handleLogout}>
              <ExitToApp />
            </IconButton>
          </>
        )}
      </Box>

      {/* Modal de carregamento */}
      <Modal
        open={isLoggingOut}
        aria-labelledby="logout-loading-modal"
        aria-describedby="logout-processing-description"
      >
        <Box
          sx={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            bgcolor: 'background.paper',
            boxShadow: 24,
            p: 4,
            borderRadius: 2,
            textAlign: 'center',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center'
          }}
        >
          <CircleLoader color="#28da3a" size={25} />
          <Typography variant="h6" sx={{ mt: 2 }}>
            Saindo... Por favor, aguarde.
          </Typography>
        </Box>
      </Modal>
    </Box>
  );
}

export default Header;
