import React, { useState } from 'react';
import { Box, Typography, Paper, Modal } from '@mui/material';
import WifiIcon from '@mui/icons-material/Wifi';
import ReceiptIcon from '@mui/icons-material/Receipt';
import PersonIcon from '@mui/icons-material/Person';
import { useNavigate } from 'react-router-dom';
import { CircleLoader } from 'react-spinners'; // Certifique-se de que isto está correto

function AcessoRapido() {
  const navigate = useNavigate();

  // Estado para controlar o modal e as mensagens de carregamento
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [loadingMessage, setLoadingMessage] = useState('');

  const atualizarDados = async () => {
    console.log('Função atualizarDados chamada');

    // Abrir o modal com a mensagem animada
    setLoadingMessage('Aguarde... Carregando Dados.');
    setIsModalOpen(true);

    // Tente obter o objeto `dadosCliente` do localStorage
    const dadosCliente = JSON.parse(localStorage.getItem('dadosCliente'));

    // Exibir a estrutura completa de dadosCliente
    console.log('Estrutura completa de dadosCliente (antes da atualização):', JSON.stringify(dadosCliente, null, 2));

    // Extraindo o idCliente
    const idCliente = dadosCliente?.dadosCliente?.['dados_cliente.json']?.id;

    // Procurando o serial number em várias possíveis localizações
    const serialNumber = dadosCliente?.dadosCliente?.Onu?.['ACS_completo.json']?.data?.serialNumber ||
                         dadosCliente?.dadosCliente?.Onu?.['ACS.json']?.serialNumber ||
                         dadosCliente?.dadosCliente?.Onu?.['radusuarios.json']?.registros?.[0]?.onu_mac;

    console.log('id cliente:', idCliente);
    console.log('Serial Number (onu_mac):', serialNumber);

    // Verifique se `idCliente` e `serialNumber` estão presentes
    if (!idCliente) {
      console.error('ID do cliente não encontrado.');
      setIsModalOpen(false); // Fecha o modal em caso de erro
      return;
    }
    if (!serialNumber) {
      console.error('Serial number (onu_mac) não encontrado.');
      setIsModalOpen(false); // Fecha o modal em caso de erro
      return;
    }

    // Montar o corpo da solicitação
    const requestBody = {
      deviceId: serialNumber,
      idCliente: idCliente
    };

    // Log do corpo da solicitação para verificar a estrutura
    console.log('Corpo da solicitação:', JSON.stringify(requestBody, null, 2));

    try {
      // Enviar a solicitação para o backend
      const response = await fetch('http://localhost:5000/api/v1/devices/updateWiFiData', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(requestBody)
      });

      if (response.ok) {
        const responseData = await response.json();
        console.log('Dados recebidos do backend:', responseData);

        // Atualize o localStorage com os dados recebidos do backend
        const updatedData = {
          ...dadosCliente, // Mantenha os dados existentes
          dadosCliente: {
            ...dadosCliente.dadosCliente,
            Onu: {
              ...dadosCliente.dadosCliente.Onu,
              // Atualize as informações com os dados recebidos do backend
              'ACS_completo.json': responseData.data.acsCompleto,
              'ACS_filtrado.json': responseData.data.acsFiltrado,
              'ACS.json': responseData.data.acsJson
            },
            // Adiciona os dados de Wi-Fi retornados ao localStorage
            wifiData: responseData.data.wifiData
          }
        };

        // Salve os dados atualizados no localStorage
        localStorage.setItem('dadosCliente', JSON.stringify(updatedData));

        // Log para verificar a estrutura após a atualização
        console.log('Estrutura completa de dadosCliente (após a atualização):', JSON.stringify(updatedData, null, 2));

        // Atualizar a mensagem do modal
        setLoadingMessage('Um momento, Verificando seu WI-FI.');

        // Simular um tempo para a sincronização
        setTimeout(() => {
          setIsModalOpen(false); // Fechar o modal
          // Redirecionar para a página "Meu Wi-Fi"
          navigate('/wifi');
        }, 2000);
      } else {
        const errorData = await response.json();
        console.error('Erro ao enviar dados:', errorData);
        setIsModalOpen(false); // Fechar o modal em caso de erro
      }
    } catch (error) {
      console.error('Erro ao enviar solicitação:', error);
      setIsModalOpen(false); // Fechar o modal em caso de erro
    }
  };

  return (
    <Box sx={{ padding: '20px' }}>
      <Typography variant="h6" sx={{ fontSize: '14px', fontWeight: 'bold', marginBottom: '20px', color: '#5b5b5b' }}>
        Acesso rápido
      </Typography>
      <Box className="acesso-rapido-scroll" sx={{ display: 'flex', overflowX: 'auto', gap: '15px' }}>
        <Paper
          elevation={0}
          sx={{
            minWidth: '120px',
            textAlign: 'center',
            borderRadius: '10px',
            backgroundColor: '#f5f5f5',
            margin: '0',
            cursor: 'pointer'
          }}
          onClick={async () => {
            console.log('Clique detectado');
            await atualizarDados(); // Chama a função para atualizar os dados
          }}
        >
          <WifiIcon sx={{ fontSize: 30, color: '#5b5b5b' }} />
          <Typography sx={{ marginTop: '10px', fontSize: '14px', color: '#5b5b5b' }}>
            Meu Wi-Fi
          </Typography>
        </Paper>

        {/* Outros itens de acesso rápido */}
        <Paper
          elevation={0}
          sx={{
            minWidth: '120px',
            textAlign: 'center',
            borderRadius: '10px',
            backgroundColor: '#f5f5f5',
            margin: '0',
            cursor: 'pointer'
          }}
          onClick={() => navigate('/faturas')}
        >
          <ReceiptIcon sx={{ fontSize: 30, color: '#5b5b5b' }} />
          <Typography sx={{ marginTop: '10px', fontSize: '14px', color: '#5b5b5b' }}>
            Minhas Faturas
          </Typography>
        </Paper>

        <Paper
          elevation={0}
          sx={{
            minWidth: '120px',
            textAlign: 'center',
            borderRadius: '10px',
            backgroundColor: '#f5f5f5',
            margin: '0',
            cursor: 'pointer'
          }}
          onClick={() => navigate('/detalhes')}
        >
          <PersonIcon sx={{ fontSize: 30, color: '#5b5b5b' }} />
          <Typography sx={{ marginTop: '10px', fontSize: '14px', color: '#5b5b5b' }}>
            Detalhes de Cliente
          </Typography>
        </Paper>
      </Box>

      <Modal
        open={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        aria-labelledby="loading-modal-title"
      >
        <Box
          sx={{
            position: 'absolute',
            bottom: '20px',
            left: '50%',
            transform: 'translateX(-50%)',
            width: '90%',
            maxWidth: '300px',
            bgcolor: 'background.paper',
            boxShadow: 24,
            p: 2,
            borderRadius: '12px',
            textAlign: 'center',
            display: 'flex',
            flexDirection: 'column', // Adicionado para alinhar o loader e o texto em coluna
            alignItems: 'center', // Alinha horizontalmente ao centro
            justifyContent: 'center' // Alinha verticalmente ao centro
          }}
        >
          {/* Usando o SkewLoader */}
          <CircleLoader color="#28da3a" size={25} />

          <Typography id="loading-modal-title" variant="h6" sx={{ marginTop: 2 }}>
            {loadingMessage}
          </Typography>
        </Box>
      </Modal>

    </Box>
  );
}

export default AcessoRapido;
