import React, { useEffect, useState } from 'react';
import { Box, Typography, Paper, Button, Collapse, Divider, Link, Modal } from '@mui/material';
import { Wifi, Computer } from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import { TextField, FormControlLabel, Checkbox } from '@mui/material';

const MeuWiFi = () => {
  const [dadosOnu, setDadosOnu] = useState(null);
  const [hosts, setHosts] = useState([]);
  const [expanded, setExpanded] = useState({});
  const [openModal, setOpenModal] = useState(false); // Estado para controlar o modal
  const [open, setOpen] = useState(false);
  const [secondModalOpen, setSecondModalOpen] = useState(false);
  const navigate = useNavigate();
  const [ssid2_4, setSsid2_4] = useState(''); // Estado para o SSID da rede 2.4GHz
  const [ssid5_8, setSsid5_8] = useState(''); // Estado para o SSID da rede 5.8GHz
  const [alterarAmbasFrequencias, setAlterarAmbasFrequencias] = useState(false); // Estado para verificar se deve alterar ambas as frequências
  const [successModalOpen, setSuccessModalOpen] = useState(false); // Estado para o modal de sucesso
  const [isProcessing, setIsProcessing] = useState(false); // Estado para desabilitar o botão durante o processamento
  const [isLoading, setIsLoading] = useState(false); // Adicione esta linha
  const [password2_4, setPassword2_4] = useState('');
  const [password5_8, setPassword5_8] = useState('');
  const [passwordModalOpen, setPasswordModalOpen] = useState(false);



  
  useEffect(() => {
    // Obtendo os dados do localStorage
    const dadosCliente = JSON.parse(localStorage.getItem('dadosCliente'));

    let deviceInfo = {};
    let serialNumber = 'N/A';
    let allHosts = [];
    let marca = 'N/A';
    let modelo = 'N/A';
    let uptime = 'N/A';

    // Verificação 1: tentando acessar 'ACS_filtrado.json'
    if (
        dadosCliente &&
        dadosCliente.dadosCliente &&
        dadosCliente.dadosCliente.Onu &&
        dadosCliente.dadosCliente.Onu['ACS_filtrado.json'] &&
        dadosCliente.dadosCliente.Onu['ACS_filtrado.json'].data &&
        dadosCliente.dadosCliente.Onu['ACS_filtrado.json'].data.deviceInfo
    ) {
        // Extraindo deviceInfo do 'ACS_filtrado.json'
        const deviceInfoFiltrado = dadosCliente.dadosCliente.Onu['ACS_filtrado.json'].data.deviceInfo;
        
        // Atualizando o serialNumber, marca, modelo, e uptime, se disponíveis
        if (deviceInfoFiltrado.serialNumber) serialNumber = deviceInfoFiltrado.serialNumber;
        if (deviceInfoFiltrado.manufacturer) marca = deviceInfoFiltrado.manufacturer;
        if (deviceInfoFiltrado.modelName) modelo = deviceInfoFiltrado.modelName;
        if (deviceInfoFiltrado.uptime) uptime = formatUptime(deviceInfoFiltrado.uptime);
        
        // Atualizando allHosts, se disponível
        allHosts = dadosCliente.dadosCliente.Onu['ACS.json']?.hostsData?.hosts || [];

        // Adicionando informações ao deviceInfo
        deviceInfo = { ...deviceInfo, ...deviceInfoFiltrado };

        console.log('Primeira verificação bem-sucedida:');
        console.log('Serial Number:', serialNumber);
        console.log('Marca:', marca);
        console.log('Modelo:', modelo);
        console.log('Uptime:', uptime);
    }

    // Verificação 2: acessando 'dados_basicos_ACS.json'
    if (
        dadosCliente &&
        dadosCliente.dadosCliente &&
        dadosCliente.dadosCliente.Onu &&
        dadosCliente.dadosCliente.Onu['dados_basicos_ACS.json'] &&
        Array.isArray(dadosCliente.dadosCliente.Onu['dados_basicos_ACS.json']) &&
        dadosCliente.dadosCliente.Onu['dados_basicos_ACS.json'].length > 0
    ) {
        // Extraindo deviceInfo do 'dados_basicos_ACS.json'
        const deviceInfoBasicos = dadosCliente.dadosCliente.Onu['dados_basicos_ACS.json'][0].deviceParameters;

        // Atualizando o serialNumber se não foi encontrado antes
        if (serialNumber === 'N/A') serialNumber = dadosCliente.dadosCliente.Onu['dados_basicos_ACS.json'][0].serialNumber || 'N/A';
        
        // Atualizando marca e modelo se não foram encontrados antes
        if (marca === 'N/A') marca = deviceInfoBasicos.manufacturer || 'N/A';
        if (modelo === 'N/A') modelo = deviceInfoBasicos.modelName || 'N/A';

        // Adicionando informações ao deviceInfo
        deviceInfo = { ...deviceInfo, ...deviceInfoBasicos };

        console.log('Segunda verificação bem-sucedida:');
        console.log('Serial Number:', serialNumber);
        console.log('Marca:', marca);
        console.log('Modelo:', modelo);
    }

    // Atualizando o estado com as informações da ONU, se encontrado
    setDadosOnu({ ...deviceInfo, marca, modelo, uptime, serialNumber });

    // Atualizando o estado com os hosts
    setHosts(allHosts);

    // Verificando se o serialNumber é válido antes de fazer a requisição
// Verificando se o serialNumber é válido antes de fazer a requisição
if (serialNumber && serialNumber !== 'N/A') {
    // Buscar os hosts ativos do servidor
    fetch('http://localhost:5000/api/v1/devices/getHosts', { // Ajuste a URL para a rota correta
        method: 'POST', // Use o método POST
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({ deviceId: serialNumber }) // Envie o serialNumber como deviceId
    })
    .then(response => {
        // Verifica se a resposta é bem-sucedida
        if (!response.ok) {
            throw new Error(`Erro ao buscar hosts: ${response.statusText}`);
        }
        return response.json();
    })
    .then(data => {
        // Atualizando o estado com os hosts
        setHosts(data.hosts || []);
        console.log('Hosts:', data.hosts); // Log para analisar os hosts
    })
    .catch(error => {
        console.error('Erro ao buscar hosts ativos:', error);
    });
} else {
    console.error('Serial Number inválido:', serialNumber);
}

}, []);




  

  
  

  const formatUptime = (uptimeInSeconds) => {
    const days = Math.floor(uptimeInSeconds / (3600 * 24));
    const hours = Math.floor((uptimeInSeconds % (3600 * 24)) / 3600);
    const minutes = Math.floor((uptimeInSeconds % 3600) / 60);
    return `${days}d ${hours}h ${minutes}m`;
  };

  if (!dadosOnu) {
    return <Typography>Carregando...</Typography>;
  }
  const redesWiFi = [
    { frequencia: '2.4Ghz', ssid: dadosOnu.wlanData?.['2.4']?.[0]?.ssid || 'N/A' },
    { frequencia: '5.8Ghz', ssid: dadosOnu.wlanData?.['5.8']?.[0]?.ssid || 'N/A' },
  ];

  const handleExpandClick = (frequencia) => {
    setExpanded((prevExpanded) => ({
      ...prevExpanded,
      [frequencia]: !prevExpanded[frequencia],
    }));
  };

  const ethernetHosts = hosts.filter((host) => host.interfaceType === 'Ethernet' && host.active);

  // Funções para controlar o modal
  const handleOpenModal = () => setOpenModal(true);

  const handleCloseModal = () => setOpenModal(false);

  // Função para abrir o segundo modal e fechar o primeiro
  const handleSecondModalOpen = () => {
    setOpenModal(false); // Fecha o primeiro modal
    setSecondModalOpen(true); // Abre o segundo modal
  };

  // Função para fechar o segundo modal e reabrir o primeiro, se necessário
  const handleSecondModalClose = () => {
    setSecondModalOpen(false); // Fecha o segundo modal
    setOpenModal(true); // Reabre o primeiro modal, se necessário
  };
  
  
    // Função para mostrar o modal de sucesso e fechar os outros
    const handleSuccess = () => {
      setSecondModalOpen(false); // Fecha o segundo modal
      setSuccessModalOpen(true); // Abre o modal de sucesso
    };

  // Função para fechar o modal de sucesso
  const handleCloseSuccessModal = () => {
    setSuccessModalOpen(false);
  };

// Funções para abrir e fechar o modal de alterar senha
  const handlePasswordModalOpen = () => {
    setOpenModal(false); // Fecha o modal atual se estiver aberto
    setPasswordModalOpen(true);
  };
  const handlePasswordModalClose = () => setPasswordModalOpen(false);

  
  const handleAlterarSenha = async () => {
    // Tente obter o objeto `dadosCliente` do localStorage
    const dadosCliente = JSON.parse(localStorage.getItem('dadosCliente'));
  
    // Log para verificar a estrutura completa de `dadosCliente`
    console.log('Estrutura completa de dadosCliente:', JSON.stringify(dadosCliente, null, 2));
  
    // Tente acessar o serialNumber da mesma maneira que você acessou com sucesso anteriormente
    const serialNumber = dadosOnu.serialNumber;
  
    console.log('Serial Number:', serialNumber); // Log para verificar o serialNumber
  
    // Certifique-se de que pelo menos uma senha foi fornecida
    if (!password2_4 && !password5_8) {
      console.error('Pelo menos uma senha deve ser fornecida.');
      return;
    }
  
    // Montar o corpo da solicitação dinamicamente
    const wifiPasswordParameters = {};
  
    if (alterarAmbasFrequencias) {
      wifiPasswordParameters["InternetGatewayDevice.LANDevice.1.WLANConfiguration.1.PreSharedKey.1.KeyPassphrase"] = password2_4;
      wifiPasswordParameters["InternetGatewayDevice.LANDevice.1.WLANConfiguration.5.PreSharedKey.1.KeyPassphrase"] = password2_4;
    } else {
      if (password2_4) {
        wifiPasswordParameters["InternetGatewayDevice.LANDevice.1.WLANConfiguration.1.PreSharedKey.1.KeyPassphrase"] = password2_4;
      }
      if (password5_8) {
        wifiPasswordParameters["InternetGatewayDevice.LANDevice.1.WLANConfiguration.5.PreSharedKey.1.KeyPassphrase"] = password5_8;
      }
    }
  
    // Montar o corpo da solicitação
    const requestBody = {
      [serialNumber]: {
        wifiPassword: wifiPasswordParameters,
      }
    };
  
    // Log do corpo da solicitação para verificar a estrutura
    console.log('Corpo da solicitação para alterar senha:', JSON.stringify(requestBody, null, 2));
  
    // Envie a solicitação para o backend para atualizar a senha
    try {
      const response = await fetch('http://localhost:5000/api/v1/devices/setCredentials', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(requestBody)
      });
  
      if (response.ok) {
        console.log('Senha alterada com sucesso');
        setPasswordModalOpen(false);
        setSuccessModalOpen(true);
      } else {
        const errorData = await response.json();
        console.error('Erro ao alterar senha:', errorData);
      }
    } catch (error) {
      console.error('Erro ao enviar solicitação:', error);
    }
  };
  
  
  

  const handleAlterarSSID = async () => {
    // Tente obter o objeto `dadosCliente` do localStorage
    const dadosCliente = JSON.parse(localStorage.getItem('dadosCliente'));
  
    // Log para verificar a estrutura completa de `dadosCliente`
    console.log('Estrutura completa de dadosCliente:', JSON.stringify(dadosCliente, null, 2));
  
    // Tente acessar o serialNumber da mesma maneira que você acessou com sucesso anteriormente
    const serialNumber = dadosOnu.serialNumber;
  
    console.log('Serial Number:', serialNumber); // Log para verificar o serialNumber
  
    // Verifique se o serialNumber está presente
    if (!serialNumber) {
      console.error('Serial number não encontrado.');
      return;
    }
  
    // Verifique se ao menos um SSID foi fornecido
    if (!ssid2_4 && !ssid5_8) {
      console.error('Pelo menos um SSID deve ser fornecido.');
      return;
    }
  
    // Montar o corpo da solicitação dinamicamente
    const wifiSsidList = {};
  
    if (alterarAmbasFrequencias) {
      // Se estiver alterando ambas as frequências
      wifiSsidList["InternetGatewayDevice.LANDevice.1.WLANConfiguration.1.SSID"] = ssid2_4;
      wifiSsidList["InternetGatewayDevice.LANDevice.1.WLANConfiguration.5.SSID"] = ssid2_4;
    } else {
      // Se estiver alterando uma frequência específica
      if (ssid2_4) {
        wifiSsidList["InternetGatewayDevice.LANDevice.1.WLANConfiguration.1.SSID"] = ssid2_4;
      }
      if (ssid5_8) {
        wifiSsidList["InternetGatewayDevice.LANDevice.1.WLANConfiguration.5.SSID"] = ssid5_8;
      }
    }
  
    // Montar o corpo da solicitação
    const requestBody = {
      [serialNumber]: {
        wifiSsid: wifiSsidList,
      }
    };
  
    // Log do corpo da solicitação para verificar a estrutura
    console.log('Corpo da solicitação:', JSON.stringify(requestBody, null, 2));
  
    // Desabilitar o botão enquanto a requisição está em andamento
    setIsLoading(true);
  
    try {
      // Enviar a solicitação para o backend
      const response = await fetch('http://localhost:5000/api/v1/devices/setCredentials', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
                },
        body: JSON.stringify(requestBody)
      });
  
      if (response.ok) {
        console.log('Dados enviados com sucesso');
  
        // Fechar o modal atual
        setSecondModalOpen(false);
        
        // Abrir o modal de sucesso
        setSuccessModalOpen(true);
      } else {
        const errorData = await response.json();
        console.error('Erro ao enviar dados:', errorData);
      }
    } catch (error) {
      console.error('Erro ao enviar solicitação:', error);
    } finally {
      // Habilitar o botão após a conclusão da requisição
      setIsLoading(false);
    }
  };
  
  
  
  

  return (
    <Box sx={{ padding: '20px' }}>
      <Typography variant="h6" gutterBottom>
        Meu Wi-Fi
      </Typography>
      <Typography variant="body2" color="textSecondary" gutterBottom>
        Selecione uma rede
      </Typography>

      {/* Informação da ONU */}
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          padding: '5px',
          backgroundColor: '#F1F1F1',
          borderRadius: '12px',
          marginBottom: '16px',
          border: '2px solid #05bd05',
          boxShadow: '0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12)',
          maxWidth: '78%',
          marginTop: '13px',
          color: '#5e5ecd',
          marginLeft: '15vh',
          paddingLeft: '3px'
        }}
      >
        <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
          <Typography variant="caption" color="textSecondary">
            Marca
          </Typography>
          <Typography variant="body1" color="black" sx={{ fontWeight: 'bold' }}>
            {dadosOnu.marca}
          </Typography>
        </Box>

        <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
          <Typography variant="caption" color="textSecondary">
            Modelo
          </Typography>
          <Typography variant="body1" color="black" sx={{ fontWeight: 'bold' }}>
            {dadosOnu.modelo}
          </Typography>
        </Box>

        <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
          <Typography variant="caption" color="textSecondary">
            Tempo Ligado
          </Typography>
          <Typography variant="body1" color="black" sx={{ fontWeight: 'bold' }}>
            {dadosOnu.uptime}
          </Typography>
        </Box>
      </Box>


      {/* Redes Wi-Fi */}
      {redesWiFi.map((rede, index) => {
          // Filtrando hosts ativos para 2.4 GHz e 5 GHz
          const hostsNaRede = hosts.filter((host) => {
              return host.active && (
                  (rede.frequencia === '2.4Ghz' && host.interfaceType === '802.11') ||
                  (rede.frequencia === '5.8Ghz' && host.interfaceType === '802.11ac')
              );
          });

          return (
              <Paper
                  key={index}
                  elevation={3}
                  sx={{
                      borderRadius: '12px',
                      padding: '15px',
                      display: 'flex',
                      flexDirection: 'column',
                      marginBottom: '15px',
                      backgroundColor: '#F9F9F9',
                  }}
              >
                  <Box
                      onClick={() => handleExpandClick(rede.frequencia)}
                      sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', cursor: 'pointer' }}
                  >
                      <Box>
                          <Typography variant="h6" sx={{ color: '#047B02', fontSize: '15px', fontWeight: 'bold' }}>
                              {rede.frequencia}
                          </Typography>
                          <Typography variant="body2" color="#047B02">
                              SSID: {rede.ssid} ({hostsNaRede.length} conectados)
                          </Typography>
                      </Box>
                      <Button sx={{ minWidth: '24px', color: 'green' }}>
                          <Wifi />
                      </Button>
                  </Box>
                  <Collapse in={expanded[rede.frequencia]} timeout="auto" unmountOnExit>
                      <Box sx={{ marginTop: '10px', padding: '10px', backgroundColor: '#FFF', borderRadius: '8px' }}>
                          {hostsNaRede.length > 0 ? (
                              <>
                                  {hostsNaRede.slice(0, 2).map((host, i) => {
                                      const vendorName = host.vendorName?.split(' ')[0] || 'Desconhecido';
                                      return (
                                          <Box key={i} sx={{ padding: '10px', marginBottom: '10px', border: '1px solid #ddd', borderRadius: '8px', backgroundColor: '#fff' }}>
                                              <Box sx={{ display: 'flex', alignItems: 'center', marginBottom: '5px' }}>
                                                  <Computer sx={{ marginRight: '5px', color: '#047B02' }} />
                                                  <Typography variant="body1" sx={{ fontWeight: 'bold' }}>
                                                      {host.hostName || 'Desconhecido'}
                                                  </Typography>
                                              </Box>
                                              <Divider sx={{ marginBottom: '5px' }} />
                                              <Typography variant="body2"><strong>IP:</strong> {host.ipAddress}</Typography>
                                              <Typography variant="body2"><strong>MAC:</strong> {host.macAddress}</Typography>
                                              <Typography variant="body2"><strong>Fornecedor:</strong> {vendorName}</Typography>
                                          </Box>
                                      );
                                  })}
                                  {hostsNaRede.length > 2 && (
                                      <Box sx={{ display: 'flex', justifyContent: 'center', marginTop: '5px' }}>
                                          <Link
                                              component="button"
                                              variant="body2"
                                              onClick={() => navigate(`/dispositivos/${rede.frequencia.toLowerCase()}`)}
                                              sx={{ color: '#047B02', fontWeight: 'bold' }}
                                          >
                                              Veja Mais &gt;
                                          </Link>
                                      </Box>
                                  )}
                              </>
                          ) : (
                              <Typography variant="body2">Nenhum host conectado</Typography>
                          )}
                      </Box>
                  </Collapse>
              </Paper>
          );
      })}


      {/* Dispositivos via Cabo */}
      {ethernetHosts.length > 0 && (
        <Paper
          elevation={3}
          sx={{
            borderRadius: '12px',
            padding: '15px',
            display: 'flex',
            flexDirection: 'column',
            marginBottom: '15px',
            backgroundColor: '#F9F9F9',
          }}
        >
          <Box
            onClick={() => handleExpandClick('Ethernet')}
            sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', cursor: 'pointer' }}
          >
            <Box>
              <Typography variant="h6" sx={{ color: '#047B02', fontSize: '15px', fontWeight: 'bold' }}>
                Dispositivos via Cabo ({ethernetHosts.length} conectados)
              </Typography>
            </Box>
            <Button sx={{ minWidth: '24px', color: 'green' }}>
              <Computer />
            </Button>
          </Box>
          <Collapse in={expanded['Ethernet']} timeout="auto" unmountOnExit>
            <Box sx={{ marginTop: '10px', padding: '10px', backgroundColor: '#FFF', borderRadius: '8px' }}>
              {ethernetHosts.slice(0, 2).map((host, i) => {
                const vendorName = host.vendorName?.split(' ')[0] || 'Desconhecido';
                return (
                  <Box key={i} sx={{ padding: '10px', marginBottom: '10px', border: '1px solid #ddd', borderRadius: '8px', backgroundColor: '#fff' }}>
                    <Box sx={{ display: 'flex', alignItems: 'center', marginBottom: '5px' }}>
                      <Computer sx={{ marginRight: '5px', color: '#047B02' }} />
                      <Typography variant="body1" sx={{ fontWeight: 'bold' }}>
                        {host.hostName || 'Desconhecido'}
                      </Typography>
                    </Box>
                    <Divider sx={{ marginBottom: '5px' }} />
                    <Typography variant="body2"><strong>IP:</strong> {host.ipAddress}</Typography>
                    <Typography variant="body2"><strong>MAC:</strong> {host.macAddress}</Typography>
                    <Typography variant="body2"><strong>Fornecedor:</strong> {vendorName}</Typography>
                  </Box>
                );
              })}
              {ethernetHosts.length > 2 && (
                <Box sx={{ display: 'flex', justifyContent: 'center', marginTop: '5px' }}>
                  <Link
                    component="button"
                    variant="body2"
                    onClick={() => navigate(`/dispositivos/ethernet`)}
                    sx={{ color: '#047B02', fontWeight: 'bold' }}
                  >
                    Veja Mais &gt;
                  </Link>
                </Box>
              )}
            </Box>
          </Collapse>
        </Paper>
      )}

      {/* Botão para abrir o modal */}
      <Box sx={{ display: 'flex', justifyContent: 'flex-end', marginTop: '10px', marginRight: '15vh' }}>
        <Button variant="contained" sx={{ backgroundColor: '#05bd05' }} onClick={handleOpenModal}>
          Alterar Dados
        </Button>
      </Box>

      {/* Primeiro Modal */}
      <Modal
        open={openModal}
        onClose={() => setOpenModal(false)}
        aria-labelledby="modal-title"
        aria-describedby="modal-description"
      >
        <Box
          sx={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            width: 300,
            bgcolor: 'background.paper',
            boxShadow: 24,
            p: 4,
            borderRadius: '12px',
            textAlign: 'center'
          }}
        >
          <Typography id="modal-title" variant="h6" component="h2" sx={{ marginBottom: 2 }}>
            Como deseja prosseguir?
          </Typography>

          <Button
            variant="contained"
            fullWidth
            onClick={handleSecondModalOpen} // Abre o segundo modal e fecha o primeiro
            sx={{
              backgroundColor: '#05bd05',
              color: '#fff',
              marginBottom: 2,
              justifyContent: 'space-between',
              padding: '10px',
              borderRadius: '15px'
            }}
            endIcon={
              <img 
                src="https://i.ibb.co/BzfCG6M/sinal-wifi-1.png"
                alt="Ícone"
                style={{ width: '29px', height: '30px', paddingRight: '20px' }}
              />
            }
          >
            Alterar nome do Wi-Fi
          </Button>
                      {/* Novo Botão para Alterar a Senha do Wi-Fi */}
          <Button
            variant="contained"
            fullWidth
            onClick={handlePasswordModalOpen} // Função que abre o modal para mudar a senha do Wi-Fi
            sx={{
              backgroundColor: '#05bd05',
              color: '#fff',
              display: 'flex',
              justifyContent: 'space-between',
              padding: '10px',
              borderRadius: '15px'
            }}
            endIcon={
              <img 
                src="https://i.ibb.co/9qpS8gc/senha-1.png"
                alt="Ícone"
                style={{ width: '29px', height: '30px', paddingRight: '20px' }}
              />
            }
          >
            Alterar senha do Wi-Fi
          </Button>
        </Box>
      </Modal>

      {/* Segundo Modal */}
      <Modal open={secondModalOpen} onClose={handleSecondModalClose} aria-labelledby="second-modal-title">
        <Box
          sx={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            width: 300,
            bgcolor: 'background.paper',
            boxShadow: 24,
            p: 4,
            borderRadius: '12px',
            textAlign: 'center'
          }}
        >
          <Typography id="second-modal-title" variant="h6" component="h2" sx={{ marginBottom: 2 }}>
            Alterar SSID
          </Typography>

          {/* Se o checkbox estiver selecionado, exibe apenas um campo */}
          {alterarAmbasFrequencias ? (
            <TextField
              fullWidth
              variant="outlined"
              placeholder="SSID 2.4Ghz e 5.8Ghz"
              value={ssid2_4}
              onChange={(e) => setSsid2_4(e.target.value)}
              sx={{ marginBottom: 2 }}
            />
          ) : (
            <>
              <TextField
                fullWidth
                variant="outlined"
                placeholder="SSID 2.4Ghz"
                value={ssid2_4}
                onChange={(e) => setSsid2_4(e.target.value)}
                sx={{ marginBottom: 2 }}
              />
              <TextField
                fullWidth
                variant="outlined"
                placeholder="SSID 5.8Ghz"
                value={ssid5_8}
                onChange={(e) => setSsid5_8(e.target.value)}
                sx={{ marginBottom: 2 }}
              />
            </>
          )}

          <FormControlLabel
            control={
              <Checkbox
                checked={alterarAmbasFrequencias}
                onChange={(e) => setAlterarAmbasFrequencias(e.target.checked)}
                name="alterarAmbasFrequencias"
                color="primary"
              />
            }
            label="Alterar 2G e 5G"
          />

        <Button
          variant="contained"
          fullWidth
          sx={{
            backgroundColor: '#05bd05',
            color: '#fff',
            padding: '10px',
            borderRadius: '15px',
            marginTop: 2
          }}
          onClick={handleAlterarSSID}
          disabled={isLoading} // Desabilita o botão durante o carregamento
        >
          Alterar SSID
        </Button>



        </Box>
      </Modal>

            {/* Modal para Alterar Senha do Wi-Fi */}
      <Modal open={passwordModalOpen} onClose={handlePasswordModalClose} aria-labelledby="password-modal-title">
        <Box
          sx={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            width: 300,
            bgcolor: 'background.paper',
            boxShadow: 24,
            p: 4,
            borderRadius: '12px',
            textAlign: 'center'
          }}
        >
          <Typography id="password-modal-title" variant="h6" component="h2" sx={{ marginBottom: 2 }}>
            Alterar Senha do Wi-Fi
          </Typography>

          {/* Se o checkbox estiver selecionado, exibe apenas um campo */}
          {alterarAmbasFrequencias ? (
            <TextField
              fullWidth
              variant="outlined"
              placeholder="Senha 2.4Ghz e 5.8Ghz"
              value={password2_4}
              onChange={(e) => setPassword2_4(e.target.value)}
              sx={{ marginBottom: 2 }}
            />
          ) : (
            <>
              <TextField
                fullWidth
                variant="outlined"
                placeholder="Senha 2.4Ghz"
                value={password2_4}
                onChange={(e) => setPassword2_4(e.target.value)}
                sx={{ marginBottom: 2 }}
              />
              <TextField
                fullWidth
                variant="outlined"
                placeholder="Senha 5.8Ghz"
                value={password5_8}
                onChange={(e) => setPassword5_8(e.target.value)}
                sx={{ marginBottom: 2 }}
              />
            </>
          )}

          <FormControlLabel
            control={
              <Checkbox
                checked={alterarAmbasFrequencias}
                onChange={(e) => setAlterarAmbasFrequencias(e.target.checked)}
                name="alterarAmbasFrequencias"
                color="primary"
              />
            }
            label="Alterar 2G e 5G"
          />

          <Button
            variant="contained"
            fullWidth
            sx={{
              backgroundColor: '#05bd05',
              color: '#fff',
              padding: '10px',
              borderRadius: '15px',
              marginTop: 2
            }}
            onClick={handleAlterarSenha} // Função para enviar a solicitação de alteração de senha
            disabled={isLoading} // Desabilita o botão durante o carregamento
          >
            Alterar Senha
          </Button>
        </Box>
      </Modal>



         {/* Modal de Sucesso */}
          <Modal
          open={successModalOpen}
          onClose={handleCloseSuccessModal}
          aria-labelledby="success-modal-title"
          aria-describedby="success-modal-description"
        >
          <Box
            sx={{
              position: 'absolute',
              top: '50%',
              left: '50%',
              transform: 'translate(-50%, -50%)',
              width: 300,
              bgcolor: 'background.paper',
              boxShadow: 24,
              p: 4,
              borderRadius: '12px',
              textAlign: 'center'
            }}
          >
            <Typography id="success-modal-title" variant="h6" component="h2" sx={{ marginBottom: 2 }}>
              Sucesso
            </Typography>
            <Typography id="success-modal-description" sx={{ marginBottom: 2 }}>
              Configurações de Wi-Fi atualizadas com sucesso!
            </Typography>
            <Button
              variant="contained"
              sx={{ backgroundColor: '#05bd05', color: '#fff' }}
              onClick={handleCloseSuccessModal}
            >
              OK
            </Button>
          </Box>
          </Modal>
    </Box>
  );
};

export default MeuWiFi;
