import React from 'react';
import { Box, Typography, Grid, Paper, Button } from '@mui/material';
import { Wifi, SignalCellularAlt, Room, ArrowBack } from '@mui/icons-material'; // Ícones
import { useNavigate } from 'react-router-dom'; // Para navegação
import Header from './Header'; // Importando o Header

function DetalhesCliente({ dadosCliente }) {
  const logins = dadosCliente?.logins || [];
  const onus = dadosCliente?.onus || [];
  const navigate = useNavigate(); // Hook para navegação

  // Funções para as cores dos ícones
  const getStatusColor = (statusInternet) => (statusInternet === 'Ativo' ? 'green' : 'red');
  const getSignalColor = (sinal) => {
    if (sinal < -28.0) return 'red';
    if (sinal >= -28.0 && sinal < -25.0) return 'yellow';
    return 'green';
  };
  const getIpColor = (ipLocal) => (ipLocal.startsWith('172') ? 'red' : 'green');

  return (
    <div>
      <Header dadosCliente={dadosCliente} /> {/* Exibe o header com os dados do cliente */}
      
      <Box sx={{ padding: '20px' }}>
        {/* Botão de Voltar */}
        <Button
                variant="contained"
                startIcon={<ArrowBack />}
                onClick={() => navigate(-1)} // Função para voltar à página anterior
                sx={{
                    marginBottom: '20px',
                    backgroundColor: '#05C53B', // Cor de fundo azul personalizada
                    color: '#fff', // Cor do texto
                    padding: '10px 10px', // Espaçamento personalizado
                    borderRadius: '20px', // Arredondamento das bordas
                    fontSize: '12px', // Tamanho da fonte
                    fontWeight: 'bold', // Negrito no texto
                    boxShadow: '0px 4px 6px rgba(0, 0, 0, 0.1)', // Sombra sutil
                    '&:hover': {
                    backgroundColor: '#0277bd', // Cor de fundo ao passar o mouse
                    }
                }}
                >
                Voltar
                </Button>


        <Typography variant="h5" gutterBottom>
          Detalhes do Cliente
        </Typography>
        
        {/* Lista de Logins */}
        <Typography variant="h6" gutterBottom>
          Logins
        </Typography>
        {logins.map((loginItem, index) => {
          const loginKey = Object.keys(loginItem)[0];
          const loginData = loginItem[loginKey][loginKey]; // Acessa os dados corretamente dentro de loginItem
          
          // Acessando os dados de status, sinal e IP corretamente
          const statusInternet = loginData?.status_online || 'Offline';
          const sinal = dadosCliente?.onus?.[0]?.['Onu 1']?.sinal_rx || 'N/A'; // Sinal extraído da ONU
          const ipLocal = loginData?.ip || 'N/A';

          return (
            <Paper key={index} elevation={3} sx={{ marginBottom: '15px', padding: '20px', borderRadius: '10px', backgroundColor: '#f0f0f0' }}>
              <Grid container spacing={2} alignItems="center">                
                {/* Status */}
                <Grid item xs={12} style={{ display: 'flex', justifyContent: 'flex-end', alignItems: 'center' }}>
                  <Typography variant="body2" color="textSecondary" sx={{ marginRight: '10px' }}>
                    <strong>Status:</strong> {statusInternet}
                  </Typography>
                </Grid>

                {/* Login */}
                <Grid item xs={12} style={{ display: 'flex', justifyContent: 'flex-end', alignItems: 'center' }}>
                  <Wifi sx={{ fontSize: '18px', color: getStatusColor(statusInternet), marginRight: '8px' }} />
                  <Typography variant="body2" color="textSecondary">
                    <strong>Login:</strong> {loginData.login}
                  </Typography>
                </Grid>

                {/* Sinal */}
                <Grid item xs={12} style={{ display: 'flex', justifyContent: 'flex-end', alignItems: 'center' }}>
                  <SignalCellularAlt sx={{ fontSize: '18px', color: getSignalColor(sinal), marginRight: '8px' }} />
                  <Typography variant="body2" color="textSecondary">
                    <strong>Sinal:</strong> {sinal} dBm
                  </Typography>
                </Grid>

                {/* IP Local */}
                <Grid item xs={12} style={{ display: 'flex', justifyContent: 'flex-end', alignItems: 'center' }}>
                  <Room sx={{ fontSize: '18px', color: getIpColor(ipLocal), marginRight: '8px' }} />
                  <Typography variant="body2" color="textSecondary">
                    <strong>IP Local:</strong> {ipLocal}
                  </Typography>
                </Grid>
              </Grid>
            </Paper>
          );
        })}

        {/* Lista de ONUs */}
        <Typography variant="h6" gutterBottom>
          ONUs
        </Typography>
        {onus.map((onuItem, index) => {
          const onuKey = Object.keys(onuItem)[0];
          const onuData = onuItem[onuKey];
          return (
            <Paper key={index} elevation={3} sx={{ marginBottom: '15px', padding: '20px', borderRadius: '10px', backgroundColor: '#f0f0f0' }}>
              <Grid container spacing={2} alignItems="center">
                <Grid item xs={12}>
                  <Typography variant="body1"><strong>ONU Nome:</strong> {onuData.nome}</Typography>
                  <Typography variant="body1"><strong>Sinal RX:</strong> {onuData.sinal_rx} dBm</Typography>
                  <Typography variant="body1"><strong>Distância:</strong> {onuData.distancia_onu}</Typography>
                </Grid>

                <Grid item xs={12} style={{ display: 'flex', justifyContent: 'flex-end', alignItems: 'center' }}>
                  <Room sx={{ fontSize: '18px', color: 'orange', marginRight: '8px' }} />
                  <Typography variant="body2" color="textSecondary">
                    <strong>Distância:</strong> {onuData.distancia_onu}
                  </Typography>
                </Grid>
              </Grid>
            </Paper>
          );
        })}
        
      </Box>
    </div>
  );
}

export default DetalhesCliente;
